import React from 'react';
import "./Projects.css"

export default function Projects() {
    return <div className="Projects cv-container r-flex r-flex--full r-flex--col">
        <div className="cv-content r-flex r-flex--full">
            <div className="cv-card r-flex pos-abs">
                <div className="mySlides active fade">
                    <img alt="" src="./profile.jpg" />
                </div>
                <a href="null" className="prev">&#10094;</a>
                <a href="null" className="next">&#10095;</a>
            </div>
        </div>
        <div className="r-flex-align--center r-flex r-flex-just--center">
            <span className="dot"></span>
            <span className="dot"></span>
            <span className="dot"></span>
        </div>
    </div>
}