import React from "react";
import { Route } from "react-router-dom";
import CVMulty from "../components/CVMulty/CVMulty";
import Projects from "../components/Projects/Projects";
import CVSingle from "../components/CVSingle/CVSingle";

export const routes = [
    {
        path: "/resume-single",
        component: CVSingle
    },
    {
        path: "/resume-multy",
        component: CVMulty
    },
    {
        path: "/projects",
        component: Projects
    },
    {
        path: "/libraries",
        component: Projects
    }
];

export function RouteWithSubRoutes(route) {
    return <Route path={route.path}
            render={props => (<route.component {...props} routes={route.routes} /> )} />
}