import React from 'react';
import { Link } from 'react-router-dom';
import './Header.css'

function Header() {
    return <div className="Header r-flex">
        <div className="head-content r-flex r-flex--full">
            <div className="head-left r-flex r-flex--full"></div>
            <div className="head-right r-flex r-flex--full r-flex-just--around">
                <div className="head--single">
                    <Link to="/resume-single">CV - Single Page</Link>
                </div>
                <div className="head--multiple">
                    <Link to="/resume-multy">CV - Multiple Page</Link>
                </div>
                <div className="head--project">
                    <Link to="/projects">Projects</Link>
                </div>
                <div className="head--library">
                    <Link to="/libraries">Libraries</Link>
                </div>
            </div>
        </div>
    </div>
}

export default Header;